
@font-face{font-family:'Bodoni';font-style:normal;font-weight:400;src:url(./assets/font/Bodoni/Bodoni.eot);src:local("Bodoni"),url(./assets/font/Bodoni/Bodoni.eot?#iefix) format("embedded-opentype"),url(./assets/font/Bodoni/Bodoni.woff) format("woff"),url(./assets/font/Bodoni/Bodoni.ttf) format("truetype"),url(./assets/font/Bodoni/Bodoni.svg#Bodoni) format("svg")
}
*{-webkit-tap-highlight-color:transparent
}
html,body{font-size:16px;height:100%;background:#fff
}
html{-webkit-overflow-scrolling:touch;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;height:100%;overflow-y:scroll
}
body{font-family:"Source Han Sans", "思源黑体", "Hiragino Sans GB", "冬青黑体", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", sans-serif;line-height:1.6;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%;font-style:italic;margin:0
}
h1,h2,h3,h4,h5,h6{font-size:1rem;font-weight:normal;margin:0
}
a{text-decoration:none;color:#16161f
}
a:hover{color:#16161f
}
a:visited{color:#16161f
}
.card{-webkit-box-shadow:0 2px 7px rgba(0,0,0,0.2);box-shadow:0 2px 7px rgba(0,0,0,0.2)
}
.align-center{text-align:center
}
#app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
.siteHeader{height:100px;margin:0 auto;*zoom:1;padding:100px 0;display:table;text-align:center
}
.siteHeader:after{content:".";display:block;clear:both;height:0;visibility:hidden;overflow:hidden
}
.siteHeader h2{font-family:'Bodoni', "Source Han Sans", "思源黑体", "Hiragino Sans GB", "冬青黑体", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", serif;font-size:1.3125rem;display:table-cell;vertical-align:middle;height:100px;line-height:100px
}
.siteHeader h2 a{color:#16161f
}
.siteHeader .line{background:url(./assets/line.png) no-repeat;-webkit-background-size:100px 100px;background-size:100px 100px;display:table-cell;vertical-align:middle;width:100px;height:100px;line-height:100px
}
.siteHeader .rightBlock{display:table-cell;vertical-align:middle;font-family:'Bodoni', "Source Han Sans", "思源黑体", "Hiragino Sans GB", "冬青黑体", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", serif;font-size:1.125rem;font-weight:100
}
.siteHeader .rightBlock a.active-menu{color:#656575
}
.siteHeader .rightBlock h1{text-align:center;font-size:1.125rem
}
.siteHeader .rightBlock h1 a{color:#16161f
}
.siteHeader .rightBlock .nav span{padding:0 4px
}
@-webkit-keyframes piston{
