}
}
@-o-keyframes piston_rev{
0%{-webkit-transform:rotate(22deg) translate3d(0, 25px, 0);-o-transform:rotate(22deg) translate3d(0, 25px, 0);transform:rotate(22deg) translate3d(0, 25px, 0)
}
50%{-webkit-transform:rotate(22deg) translate3d(0, -25px, 0);-o-transform:rotate(22deg) translate3d(0, -25px, 0);transform:rotate(22deg) translate3d(0, -25px, 0)
}
100%{-webkit-transform:rotate(22deg) translate3d(0, 25px, 0);-o-transform:rotate(22deg) translate3d(0, 25px, 0);transform:rotate(22deg) translate3d(0, 25px, 0)
}
}
@keyframes piston_rev{
0%{-webkit-transform:rotate(22deg) translate3d(0, 25px, 0);-o-transform:rotate(22deg) translate3d(0, 25px, 0);transform:rotate(22deg) translate3d(0, 25px, 0)
}
50%{-webkit-transform:rotate(22deg) translate3d(0, -25px, 0);-o-transform:rotate(22deg) translate3d(0, -25px, 0);transform:rotate(22deg) translate3d(0, -25px, 0)
}
100%{-webkit-transform:rotate(22deg) translate3d(0, 25px, 0);-o-transform:rotate(22deg) translate3d(0, 25px, 0);transform:rotate(22deg) translate3d(0, 25px, 0)
}
}
.loadingLines{position:absolute;left:50%;top:50%
}
.loadingLines.show div{position:absolute;left:0;top:-30px;width:1px;height:60px;background:#16161f
}
.loadingLines.show .line1{margin-top:-6px;-webkit-animation:piston .9s ease-in-out infinite;-o-animation:piston .9s ease-in-out infinite;animation:piston .9s ease-in-out infinite
}
.loadingLines.show .line2{margin-top:6px;-webkit-animation:piston_rev .9s ease-in-out infinite;-o-animation:piston_rev .9s ease-in-out infinite;animation:piston_rev .9s ease-in-out infinite
}



